import React, { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";
import { Icon } from "common/components/Icons";
import ArrowRightIcon from "common/components/Icons/icons/24/arrow-right.svg";

import { SmallBannerProps } from "./SmallBanner.types";
import styles from "./SmallBanner.module.scss";

const cx = classNames.bind(styles);

export const SmallBanner: FunctionComponent<SmallBannerProps> = ({
    text,
    link,
    onClick,
    className,
}) => {
    const content = (
        <div className={cx("content")} onClick={onClick}>
            <Text className={cx("text")}>{text}</Text>

            <Icon>
                <ArrowRightIcon />
            </Icon>
        </div>
    );

    if (link) {
        return (
            <a className={cx("root", className)} href={link}>
                {content}
            </a>
        );
    }

    return (
        <button className={cx("root", className)} onClick={onClick}>
            {content}
        </button>
    );
};
