import React, { FC, useEffect, useRef } from "react";
import classNames from "classnames/bind";

import { convertQueryStringToQueryObject } from "common/utils/queryString";
import {
    PAGES_HAS_TRADEDOUBLER_PIXEL,
    setValueOfCookieTduid,
} from "common/services/TradedoublerPixelService";
import { Container } from "common/components/Container";
import { HowItWorks } from "common/components/HowItWorks";
import { OurResults } from "common/components/new/OurResults";
import { NewAppointment } from "common/components/NewAppointment";
import { MaxWidthContainer } from "common/components/new/MaxWidthContainer";
import { HowItWorksProvider } from "common/components/HowItWorks/hooks/context";
import { OurClinicsWithTabs } from "common/components/new/OurClinicsWithTabs";
import { useComputedFixedButtonOffset } from "common/hooks/useComputedFixedButtonOffset";
import { ExperimentService } from "common/services/ExperimentService";
import { useFixedButtonContext } from "common/context/fixedButtonContext";
import { PriceBlock } from "common/components/new/PriceBlock";
import { CountryVersion, Theme } from "common/constants/enum";
import { CheckUp } from "common/components/new/CheckUp";
import { Heading } from "common/components/new/Heading";

import { Footer } from "components/Footer";

import { Head } from "./components/Head";
import { Doctors } from "./components/Doctors";
import { UniversalTVLayoutProps } from "./UniversalTVLayout.types";
import styles from "./UniversalTVLayout.module.scss";

const cx = classNames.bind(styles);

export const UniversalTVLayout: FC<UniversalTVLayoutProps> = ({
    content,
    version,
    language,
}) => {
    const {
        head,
        checkUp,
        prices,
        doctors,
        results,
        contacts,
        howItWorks,
        appointment,
        ourClinicsWithTabs,
    } = content;
    const hasWindow = typeof window !== "undefined";

    const appointmentRef = useRef<HTMLDivElement>(null);

    const {
        isFixedButtonActive,
        scrollToAppointment,
    } = useComputedFixedButtonOffset(appointmentRef);

    const { setFixedButton } = useFixedButtonContext();

    useEffect(() => {
        setFixedButton(isFixedButtonActive);
    }, [isFixedButtonActive]);

    useEffect(() => {
        if (!hasWindow) {
            return;
        }

        const { pathname, search } = location;
        const queryParams = convertQueryStringToQueryObject(search);
        const { tduid } = queryParams;

        if (PAGES_HAS_TRADEDOUBLER_PIXEL.includes(pathname) && tduid) {
            setValueOfCookieTduid(tduid);
        }
    }, [hasWindow]);

    return (
        <div className={cx("root")}>
            <ExperimentService />

            <MaxWidthContainer>
                <div className={cx("head")}>
                    <Head
                        {...head}
                        theme={Theme.LIGHT}
                        contacts={contacts}
                        onScrollToAppointment={scrollToAppointment}
                        isFixedButtonActive={isFixedButtonActive}
                    />
                </div>
                <CheckUp
                    className={cx("check-up")}
                    {...checkUp}
                    onButtonClick={scrollToAppointment}
                />
                <div className={cx("appointment")} ref={appointmentRef}>
                    <Container>
                        <Heading
                            className={cx("appointment-title", {
                                "appointment-title_ss-pro":
                                    version === CountryVersion.GERMANY,
                                "appointment-title_germany":
                                    version === CountryVersion.GERMANY,
                            })}
                        >
                            {appointment.title}
                        </Heading>
                        <NewAppointment
                            version={version}
                            language={language}
                            {...appointment}
                            className={cx("form")}
                        />
                    </Container>
                </div>
                <Container className={cx("prices")}>
                    <PriceBlock
                        {...prices}
                        version={version}
                        language={language}
                        onButtonClick={scrollToAppointment}
                    />
                </Container>
                <HowItWorksProvider>
                    <HowItWorks
                        {...howItWorks}
                        className={cx("how-it-works")}
                    />
                </HowItWorksProvider>
                <Container className={cx("container")}>
                    <OurResults {...results} />
                </Container>
                <div className={cx("doctors")}>
                    <Doctors {...doctors} version={version} />
                </div>
                {ourClinicsWithTabs && (
                    <OurClinicsWithTabs
                        {...ourClinicsWithTabs}
                        className={cx("container")}
                    />
                )}
                <Container>{contacts && <Footer {...contacts} />}</Container>
            </MaxWidthContainer>
        </div>
    );
};
