import { NextPageContext } from "next";
import { mergeDeepRight } from "ramda";
import content from "content";
import loadNamespaces from "next-translate/loadNamespaces";
import useTranslation from "next-translate/useTranslation";

import { Language } from "common/constants/enum";

import { BaseLayout, dictionary } from "layouts/BaseLayout";
import {
    UniversalTVLayout,
    UniversalTVLayoutContentProps,
} from "layouts/UniversalTVLayout";

import { PageProps } from "../page.type";

const NewMain = ({
    version,
    contentFolder,
    language,
    title,
    description,
}: PageProps) => {
    const { t } = useTranslation();

    const currentLanguage =
        Language[language?.toUpperCase() as keyof typeof Language];

    const pageContent = contentFolder
        ? content[contentFolder][version](t, {
              version,
              language: currentLanguage,
          })
        : {};
    const baseContent = dictionary[version](t, {
        version,
        language: currentLanguage,
    });

    const currentContent = mergeDeepRight(pageContent, baseContent);

    return (
        <BaseLayout
            title={title}
            description={description}
            content={baseContent}
        >
            <UniversalTVLayout
                content={currentContent as UniversalTVLayoutContentProps}
                version={version}
                language={currentLanguage}
            />
        </BaseLayout>
    );
};

NewMain.getInitialProps = async ({ query, pathname }: NextPageContext) => ({
    ...query,
    ...(await loadNamespaces({ locale: query.locale as string, pathname })),
});

export default NewMain;
