import { FC, useRef, useState } from "react";
import classnames from "classnames/bind";

import { Direction, WINDOW_SIZE } from "common/constants/enum";
import { sideScroll } from "common/utils/sideScroll";
import { useWindowDimensions } from "common/hooks/useWindowDimensions";
import { Picture } from "common/components/Picture";
import { CountryVersion } from "common/constants/enum";
import { Container } from "common/components/Container";
import { Icon } from "common/components/Icons";
import BadgetEs from "common/components/Icons/icons/badget-es.svg";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Text } from "common/components/Text";
import { BenefitsList } from "common/components/new/BenefitsList";
import ArrowRightIcon from "common/components/Icons/icons/24/arrow-right.svg";
import ArrowLeftIcon from "common/components/Icons/icons/24/arrow-left.svg";

import styles from "./Doctors.module.scss";
import { DoctorsProps } from "./Doctors.types";

const cx = classnames.bind(styles);

const iconsBadgetByVersion = {
    [CountryVersion.SPAIN]: <BadgetEs />,
};

const WIDTH_SCROLL_BLOCK = 1076;
const WIDTH_ITEM_DESKTOP = 403;
const WIDTH_ITEM_MOBILE = 231;
const SPEED_SMOOTH_SLIDER = 3;
const STEP_SLIDER = 10;
const COUNT_OF_SCROLLED_SLIDER = 3;

export const Doctors: FC<DoctorsProps> = ({
    title,
    description: { benefit1, benefit2, benefit3, benefit4, benefit5 },
    images,
    version,
}) => {
    const { width } = useWindowDimensions();
    const [isDisabledPrevButton, setDisabledPrevButton] = useState(true);
    const [isDisabledNextButton, setDisabledNextButton] = useState(false);
    const scrollRef = useRef<HTMLDivElement>(null);

    const handleScrollNext = () => {
        sideScroll(
            scrollRef.current,
            Direction.RIGHT,
            SPEED_SMOOTH_SLIDER,
            width >= WINDOW_SIZE.DESKTOP
                ? WIDTH_ITEM_DESKTOP * COUNT_OF_SCROLLED_SLIDER
                : WIDTH_ITEM_MOBILE,
            STEP_SLIDER,
            setDisabledPrevButton,
            setDisabledNextButton,
            WIDTH_SCROLL_BLOCK
        );
    };

    const handleScrollPrev = () => {
        sideScroll(
            scrollRef.current,
            Direction.LEFT,
            SPEED_SMOOTH_SLIDER,
            width >= WINDOW_SIZE.DESKTOP
                ? WIDTH_ITEM_DESKTOP * COUNT_OF_SCROLLED_SLIDER
                : WIDTH_ITEM_MOBILE,
            STEP_SLIDER,
            setDisabledPrevButton,
            setDisabledNextButton,
            WIDTH_SCROLL_BLOCK
        );
    };

    return (
        <section>
            <Container className={cx("benefits-list")}>
                <BenefitsList
                    title={title}
                    list={{ benefit1, benefit2, benefit3, benefit4, benefit5 }}
                />
            </Container>
            <Container>
                <Icon className={cx("icon")}>
                    {iconsBadgetByVersion[version as CountryVersion.SPAIN]}
                </Icon>
                <div className={cx("arrows")}>
                    <button
                        onClick={handleScrollPrev}
                        className={cx("arrow")}
                        disabled={isDisabledPrevButton}
                    >
                        <Icon className={cx("arrow__icon")}>
                            <ArrowLeftIcon />
                        </Icon>
                    </button>
                    <button
                        onClick={handleScrollNext}
                        className={cx("arrow")}
                        disabled={isDisabledNextButton}
                    >
                        <Icon className={cx("arrow__icon")}>
                            <ArrowRightIcon />
                        </Icon>
                    </button>
                </div>
            </Container>
            <div className={cx("container-slider")}>
                <Container className={cx("container-default")}>
                    <div ref={scrollRef} className={cx("slider")}>
                        {images.map((item) => (
                            <div className={cx("slide")} key={item.title}>
                                <Picture
                                    classNameImg={cx("slide__image")}
                                    className={cx("slide__picture")}
                                    {...item.image}
                                />
                                <Heading
                                    className={cx("slide__title")}
                                    type={HeadingType.S}
                                    tag={HeadingTag.H3}
                                >
                                    {item.title}
                                </Heading>
                                <Text className={cx("slide__description")}>
                                    {item.description}
                                </Text>
                            </div>
                        ))}
                    </div>
                </Container>
            </div>
        </section>
    );
};
