import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { Text } from "common/components/Text";
import { Icon } from "common/components/Icons";
import DoneIcon from "common/components/Icons/icons/24/done.svg";

import { CheckUpListProps } from "./CheckUpList.types";
import styles from "./CheckUpList.module.scss";

const cx = classNames.bind(styles);

export const CheckUpList: FunctionComponent<CheckUpListProps> = ({
    list,
    className,
    classNameItem,
}) => {
    return (
        <ul className={cx("root", className)}>
            {list.map((item, index) => (
                <li key={index} className={cx("item", classNameItem)}>
                    <Icon className={cx("icon")}>
                        <DoneIcon />
                    </Icon>
                    <Text className={cx("item-text")}>{item}</Text>
                </li>
            ))}
        </ul>
    );
};
