import { FC } from "react";
import classNames from "classnames/bind";

import { Heading } from "common/components/new/Heading";
import { Picture } from "common/components/Picture";
import { getAnalyticsId } from "common/utils/AnalyticsId";
import { Button, ButtonSize, ButtonTheme } from "common/components/new/Button";

import { CheckUpList } from "./components/CheckUpList";
import { CheckUpProps } from "./CheckUp.types";
import styles from "./CheckUp.module.scss";

const cx = classNames.bind(styles);

export const CheckUp: FC<CheckUpProps> = ({
    title,
    className,
    image,
    list,
    button,
    onButtonClick,
}) => (
    <div className={cx(className, "root")}>
        <Picture
            className={cx("picture")}
            classNameImg={cx("image")}
            {...image}
        />

        <div className={cx("content")}>
            <Heading className={cx("title")}>{title}</Heading>

            <CheckUpList
                className={cx("list")}
                classNameItem={cx("item")}
                list={list}
            />

            <Button
                {...button}
                id={getAnalyticsId({ zone: "check-up" })}
                size={ButtonSize.M}
                theme={ButtonTheme.RED}
                className={cx("only-desktop")}
                onClick={onButtonClick}
            />
        </div>
    </div>
);
