import { FC } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";

import { BenefitsListProps } from "./BenefitsList.types";
import styles from "./BenefitsList.module.scss";

const cx = classNames.bind(styles);

export const BenefitsList: FC<BenefitsListProps> = ({
    title,
    list,
    className,
}) => {
    const { benefit1, benefit2, benefit3, benefit4, benefit5 } = list;

    const getStyledBenefits = (benefits: string[]) => {
        return benefits.map((benefit) => {
            return (
                <li
                    key={benefit}
                    className={cx("benefit-item")}
                    dangerouslySetInnerHTML={{ __html: benefit }}
                />
            );
        });
    };

    return (
        <div className={cx(className, "root")}>
            <Heading
                type={HeadingType.XL}
                tag={HeadingTag.H2}
                className={cx("title")}
            >
                {title}
            </Heading>
            <div className={cx("benefits-flex-wrapper")}>
                <div className={cx("benefits-group")}>
                    <ul className={cx("benefits-list")}>
                        {getStyledBenefits([benefit1, benefit2, benefit3])}
                    </ul>
                </div>
                <div className={cx("benefits-group")}>
                    <ul className={cx("benefits-list")}>
                        {getStyledBenefits([benefit4, benefit5])}
                    </ul>
                </div>
            </div>
        </div>
    );
};
